<template>
  <ul>
    <TodoItem
      v-for="(todo, i) in todos"
      v-bind:todo="todo"
      v-bind:index="i"
      :key="todo.id"
      v-on:remove-todo="removeTodo"
    />
  </ul>
</template>

<script>
import TodoItem from './TodoItem.vue';

export default {
  props: ['todos'],
  components: {
    TodoItem,
  },
  methods: {
    removeTodo(id) {
      this.$emit('remove-todo', id);
    },
  },
};
</script>

<style scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 700px;
}
</style>
